import React from 'react';
import ReactDOM from 'react-dom';
import './styles.min.css';
import './index.css';
import {
  getBubbleSortAnimations
} from "./sortingAlgo/bubbleSort";
import { getMergeSortAnimations } from './sortingAlgo/sortingAlgorithms.js';

const TIME = 1;
const BARS = 500;

class Page extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      array: genArray()
    };
  }

  newArray = () => {
    this.setState({ array: genArray() });
    console.log(this.state.array);
  }
  bubbleSort() {
    const animations = getBubbleSortAnimations(this.state.array);
    for (let i = 0; i < animations.length; i++) {
      const arrayBars = document.getElementsByClassName('rec');

      const [barTwoIdx, barOneIdx] = animations[i];
      const barOneStyle = arrayBars[barOneIdx].style;
      const barTwoStyle = arrayBars[barTwoIdx].style;
      //const color = i % 3 === 0 ? '#6547c9' : 'red';
      setTimeout(() => {
        barOneStyle.backgroundColor = '#6547c9';
        barTwoStyle.backgroundColor ='#6547c9';
        setTimeout(() => {
          //console.log(arrayBars[newHeight]);
          // console.log( "old height: "+ arrayBars[barOneIdx].offsetHeight + " new height:"+ arrayBars[barTwoIdx].offsetHeight);
          var temp = arrayBars[barOneIdx].offsetHeight;
          const barOneStyle = arrayBars[barOneIdx].style;
          barOneStyle.height = `${arrayBars[barTwoIdx].offsetHeight}px`;
          barTwoStyle.height = `${temp}px`;
        }, (i + 1) * TIME)
      }

        , i * TIME);
    }
    //  const [comparison,swap] = animations[0];
    // // console.log(animations[1]);
    // console.log(comparison);
    // // console.log(swap);
    // const arrayBars = document.getElementsByClassName('rec');
    // arrayBars[comparison].style.backgroundColor = 'red';
    // console.log(arrayBars[comparison]);
    // console.log(arrayBars);
    console.log('bubbleSort');
    console.log(this.state.array);
  }
  quickSort() {
    console.log('quickSort');
  }
  mergeSort() {
    const animations = getMergeSortAnimations(this.state.array);
    for (let i = 0; i < animations.length; i++) {
      const arrayBars = document.getElementsByClassName('rec');
      const isColorChange = i % 3 !== 2;
      if (isColorChange) {
        const [barOneIdx, barTwoIdx] = animations[i];
        const barOneStyle = arrayBars[barOneIdx].style;
        const barTwoStyle = arrayBars[barTwoIdx].style;
        const color = i % 3 === 0 ? 'red' : '#6547c9';
        setTimeout(() => {
          barOneStyle.backgroundColor = color;
          barTwoStyle.backgroundColor = color;
        }, i * TIME);
      } else {
        setTimeout(() => {
          const [barOneIdx, newHeight] = animations[i];
          const barOneStyle = arrayBars[barOneIdx].style;
          barOneStyle.height = `${newHeight}px`;
        }, i * TIME);
      }
    }
  }




  render() {
    document.getElementById('genArray').onclick = () => this.newArray();
    document.getElementById('bubbleSort').onclick = () => this.bubbleSort();
    document.getElementById('quickSort').onclick = () => this.quickSort();
    document.getElementById('mergeSort').onclick = () => this.mergeSort();
    return (
      <div className="game">
        {this.state.array.map((value, idx) => (
          <div
            className="rec"
            key={idx}
            style={{
              backgroundColor: '#6547c9',
              height: `${value}px`,
              bottom: '10px',
            }}
          ></div>
        ))}
      </div>
    );
  }
}
function genArray() {
  const array = [];
  for (let i = 0; i < BARS; i++) {
    array.push(randomIntFromInterval(5, 750));
  }
  return array;
}
function randomIntFromInterval(min, max) {
  // min and max included
  return Math.floor(Math.random() * (max - min + 1) + min);
}




